@import "./../../common/sass/variables";

.headerBlock {
  width: 100%;
height: 90px;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 173px;
  display: flex;

  justify-content: center;

  @media (max-width: 891px) {
    padding: 0 40px;
  }
  .container {
    max-width: 1140px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;


    h2 {
      font-family: $QuicksandFont;
      color: $Text;
    }
    .logout{
      font-family: $QuicksandFont;
      color: $Text;
      cursor: pointer;
    }
  }
}